a {
  text-decoration: none;
  color: black;
}

.offscreen,
legend,
caption {
  overflow: hidden;
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.signin-btn,
.signup-btn {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 50px;
  padding: 20px 40px;
  background-color: #7F4B4F;
  color: #FFFCF9;
  font-size: 20px;
  font-weight: 700;
}

.signin-btn:hover,
.signup-btn:hover {
  background-color: #181818;
  color: #FFFCF9;
  background-color: #181818;
  border-radius: 50px;
  margin: 0 auto;
  border: 0;
}

body {
  overflow: hidden;
}

.landing-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffe5b3;
  flex-direction: column;
  align-items: flex-start;
  width: 100vw;
  height: 89vh;
}
.landing-container .landing-title,
.landing-container .landing-subtitle {
  width: 800px;
  color: rgba(0, 0, 0, 0.6);
  margin: 0 auto;
  margin-bottom: 50px;
}
.landing-container .landing-title {
  font-weight: 900;
  font-size: 32px;
}
.landing-container .landing-subtitle {
  font-weight: 700;
  font-size: 24px;
}
.landing-container .landing-links {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: flex-start;
  width: 800px;
  margin: 0 auto;
  margin-bottom: 80px;
}
.landing-container .landing-links .signin-btn,
.landing-container .landing-links .signup-btn {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 50px;
  padding: 20px 40px;
  background-color: #7f4b4f;
  color: #fffcf9;
  font-size: 24;
  font-weight: 400;
  margin: 0;
  display: block;
  text-align: center;
}
.landing-container .landing-links .signin-btn {
  margin-right: 20px;
}

.site-title {
  background-color: #ffe5b3;
  margin: 0;
  padding: 20px;
}

.login-container,
.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: relative;
  background: #ffe5b3;
}
.login-container .login-form,
.login-container .register-form,
.register-container .login-form,
.register-container .register-form {
  display: grid;
  width: 780px;
  padding: 65px 95px;
  background: #fff;
  box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  margin-top: 40px;
  box-sizing: border-box;
}
.login-container .login-form .username,
.login-container .login-form .password,
.login-container .login-form .email,
.login-container .register-form .username,
.login-container .register-form .password,
.login-container .register-form .email,
.register-container .login-form .username,
.register-container .login-form .password,
.register-container .login-form .email,
.register-container .register-form .username,
.register-container .register-form .password,
.register-container .register-form .email {
  line-height: 40px;
  margin-bottom: 40px;
  padding-left: 20px;
}
.login-container .login-form .email,
.login-container .register-form .email,
.register-container .login-form .email,
.register-container .register-form .email {
  margin: 0;
}
.login-container .login-form .login-form-checkbox,
.login-container .register-form .login-form-checkbox,
.register-container .login-form .login-form-checkbox,
.register-container .register-form .login-form-checkbox {
  transform: scale(1.7);
  margin-right: 10px;
  margin-left: 5px;
}
.login-container .login-form .login-btn,
.login-container .login-form .register-btn,
.login-container .register-form .login-btn,
.login-container .register-form .register-btn,
.register-container .login-form .login-btn,
.register-container .login-form .register-btn,
.register-container .register-form .login-btn,
.register-container .register-form .register-btn {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 50px;
  padding: 20px 40px;
  background-color: #eaeaea;
  color: #000000;
  font-size: 24;
  font-weight: 400;
  margin-top: 40px;
}
.login-container .title-group,
.register-container .title-group {
  position: absolute;
  display: flex;
  justify-content: center;
  top: -10px;
}
.login-container .login-title,
.login-container .register-title,
.register-container .login-title,
.register-container .register-title {
  border-right: 1px solid;
  padding: 0 40px;
  font-weight: 900;
  font-size: 64px;
  margin: 0;
  margin-top: 10px;
}
.login-container .register-title,
.register-container .register-title {
  border-right: 0;
}
.login-container .login-active,
.register-container .login-active {
  color: #7f4b4f;
}